import { Component, OnInit } from '@angular/core';
import { GetApiService } from './../services/get-api.service';
import { trigger, transition, style, animate } from '@angular/animations';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  animations:[
    trigger('leftheader',[
      transition('void => *',[
        style({opacity:0, transform:"translateX(-15px)"}),
        animate('800ms', style({opacity:1,transform:"translateY(0px)"}))
      ])
    ]),
    trigger('rightheader',[
      transition('void => *',[
        style({opacity:0, transform:"translateX(15px)"}),
        animate('800ms', style({opacity:1,transform:"translateY(0px)"}))
      ])
    ]),
    trigger('featapi',[
      transition('void => *',[
        style({transform:"scale(.6)", opacity:0}),
        animate('800ms', style({transform:"scale(1)", opacity:1}))
      ])
    ]),
    trigger('leftfeatures',[
      transition('void => *',[
        style({opacity:0, transform:"translateX(-25px)"}),
        animate('1200ms', style({opacity:1, transform:"translateX(0px)"}))
      ])
    ]),
    trigger('rightfeatures',[
      transition('void => *',[
        style({opacity:0, transform:"translateX(25px)"}),
        animate('1200ms', style({opacity:1, transform:"translateX(0px)"}))
      ])
    ])
  ]
})
export class HomeComponent implements OnInit {

  yourLink:string=''; // inputValue
  error:string; // if input value is empty (will add ... to error msg)
  result; // data come from back end
  successMsg; // success message if link is shorted (i did not use in project)
  mainLink; // the link before short
  shortLink; // link after short it
  copy; // copy btn
  forShowMsg = false; // if the link shorted successfully ( result div will appear )
  ifNotSuccess; // for msg come from back end if Not Success
  displayIfNot = false; // if the link [ Not ] shorted successfully ( result div will appear )
  ifcopy; // depend on this var to change btn back if copied

  constructor(public shortservice:GetApiService) { }
  ngOnInit() {}
  // call function from service to short the link
  shortNow(myLink){
     if(this.yourLink == ''){
       this.error = ' ...';
       document.getElementById('myIn').style.borderColor="#ff90b6";
     }else{
       //console.log(myLink.value.link)
       this.shortservice.shortAlink(myLink.value).subscribe(data => {
         this.result = data.json();
         //console.log(this.result);
         if(this.result.length > 2){
          this.forShowMsg = true; // to appear result div if success in backend
          for(let i = 0; i < 3; i++){
            this.successMsg = this.result[0];
            this.mainLink = this.result[1];
            this.shortLink = this.result[2]
            this.copy = 'copy';
            this.ifcopy=''; // to remove background of button if clicked on it to copy
          }
         }else{
           this.forShowMsg = false; // to delete result div if success in backend
           this.displayIfNot = true; // to appear result div if NoT success in backend
           for (let x in this.result){
             this.ifNotSuccess = this.result[x];
             //console.log(this.result[x]);
           }
          }
       })
       this.yourLink='www.ahmedzaher.com';
       this.error = '';
       document.getElementById('myIn').style.borderColor="#EEE";
     }
  }
  //function to copy short link to clipboard
  copyMessage(val){
    this.copy = 'copied!'; // change button text if clicked on it
    this.ifcopy = 'changeBack' // change background of button if clicked on it
    const selBox = document.createElement('textarea');
    selBox.style.position = 'fixed';
    selBox.style.left = '0';
    selBox.style.top = '0';
    selBox.style.opacity = '0';
    selBox.value = val;
    document.body.appendChild(selBox);
    selBox.focus();
    selBox.select();
    document.execCommand('copy');
    document.body.removeChild(selBox);
    // console.log('done'); // just ensure message
  }
}
