import { Component, OnInit } from '@angular/core';
import { GetApiService } from './../services/get-api.service';
import { Router, ActivatedRoute } from '@angular/router';
import { trigger, transition, style, animate } from '@angular/animations';

@Component({
  selector: 'app-special',
  templateUrl: './special.component.html',
  styleUrls: ['./special.component.scss'],
  animations:[
    trigger('show',[
      transition('void => *',[
        style({opacity:0, transform:"translateY(15px)"}),
        animate('800ms', style({opacity:1,transform:"translateY(0px)"}))
      ])
    ])
  ]
})
export class SpecialComponent implements OnInit {
  special_link; // special linke come from DB
  my_id; // receive link Id comes from myshortlinksComponent
  constructor(public service:GetApiService, public router:Router, public active:ActivatedRoute) {
    this.getNow();
   }
  ngOnInit() {}
  // call function from service that get details about special link
  getNow(){
    this.my_id = this.active.paramMap.subscribe(Param=>{
      this.service.getDetails(Param.get('id')).subscribe(data => {
        this.special_link = data.json();
      })
    });
  }
  // Back To All_Links_page[myshortlinks]
  backTo(){
    this.router.navigate(['myshortlinks']);
  }

}
